.reservation__form-paragraph--checkbox {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: relative; }

input[type="checkbox"] {
  width: 20px; }

.reservation__form-input--checkbox + label::before {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  background-color: #f9fBfD;
  top: 0;
  left: 0; }
  @media (max-width: 768px) {
