      top: 5px;
      left: 4px; } }

.reservation__form-input--checkbox:checked + label::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  background-image: url("../img/svg/tick.svg");
  background-repeat: none;
  top: 0;
  left: 0; }

